
<template>
  <div class="metadata-management-statistics-trend">
    <div>
      <h3>字段数量走势图</h3>
      <chart
        v-if="listLength"
        :options="fieldDataChart"
        :auto-resize="true"
        class="field-chart"
      />
      <div
        v-else
        class="empty-data"
      >
        暂无数据
      </div>

    </div>
    <div class="clearfix">
      <div>
        <h3>数据量走势图</h3>
        <chart
          v-if="listLength"
          :options="dataVolumeChart"
          :auto-resize="true"
          class="chart-item"
        />
        <div
          v-else
          class="empty-data"
        >
          暂无数据
        </div>
      </div>
      <!-- <div class="chart-content">
        <h3>数据来源图</h3>
        <chart
          v-if="chartData.sourceCount.data && chartData.sourceCount.data.length"
          :options="sourceCountChart"
          :auto-resize="true"
          class="chart-item"
        />
        <div
          v-else
          class="empty-data"
        >
          暂无数据
        </div>
      </div> -->
    </div>
  </div>
</template>

<script>
import './trend-chart.scss';

export default {
  props: {
    chartData: {
      type: Object,
      default() {
        return {};
      }
    },
    listLength: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      fieldDataChart: {},
      dataVolumeChart: {}
      // sourceCountChart: {}
    };
  },
  watch: {
    chartData: {
      handler: function (val) {
        this.setChartData();
      },
      deep: true
    }
  },
  created() {
  },
  methods: {
    setChartData() {
      // this.sourceCountChart = {
      //   color: ['#0c8ce8', '#00d1ff', '#0ce8cf', '#ffbd1b', '#fad55c', '#88d163', '#67ca6d'],
      //   grid: {
      //     top: 40,
      //     left: 40,
      //     right: 40,
      //     bottom: 50
      //   },
      //   tooltip: {
      //     trigger: 'item',
      //     formatter: '{b} {d}%'
      //   },
      //   legend: {
      //     type: 'scroll',
      //     bottom: 10,
      //     left: 'center',
      //     data: this.chartData.sourceCount.legend.map(item => {
      //       return {
      //         name: item,
      //         icon: 'roundRect'
      //       };
      //     })
      //   },
      //   series: [
      //     {
      //       type: 'pie',
      //       center: ['50%', '50%'],
      //       radius: ['15%', '70%'],
      //       data: this.chartData.sourceCount.data,
      //       labelLine: {
      //         normal: {
      //           show: false
      //         }
      //       },
      //       label: {
      //         normal: {
      //           show: false
      //         }
      //       },
      //       itemStyle: {
      //         emphasis: {
      //           shadowBlur: 10,
      //           shadowOffsetX: 0,
      //           shadowColor: 'rgba(0, 0, 0, 0.5)'
      //         }
      //       }
      //     },
      //     {
      //       radius: ['15%', '20%'],
      //       center: ['50%', '50%'],
      //       type: 'pie',
      //       label: {
      //         normal: {
      //           show: false
      //         },
      //         emphasis: {
      //           show: false
      //         }
      //       },
      //       labelLine: {
      //         normal: {
      //           show: false
      //         },
      //         emphasis: {
      //           show: false
      //         }
      //       },
      //       animation: false,
      //       tooltip: {
      //         show: false
      //       },
      //       itemStyle: {
      //         color: '#ddd',
      //         opacity: 0.4
      //       },
      //       data: this.chartData.sourceCount.data
      //     }
      //   ]};
      this.fieldDataChart = {
        color: ['#0092ff'],
        grid: {
          top: 40,
          left: 40,
          right: 40,
          bottom: 50
        },
        tooltip: {
          trigger: 'axis',
          backgroundColor: '#fff',
          textStyle: {
            color: '#666',
            fontSize: 12
          },
          axisPointer: {
            lineStyle: {
              color: '#dcdcdc'
            }
          },
          formatter: function (params) {
            return `${params[0].axisValue} <br/> ${params[0].marker}字段数量：${params[0].data}`;
          }
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: this.chartData.fieldData.date,
          axisTick: {
            show: false
          },
          axisLabel: {
            color: '#666',
            fontSize: 12
          },
          axisLine: {
            lineStyle: {
              color: '#ddd'
            }
          }
        },
        yAxis: {
          type: 'value',
          axisTick: {
            show: false
          },
          axisLabel: {
            color: '#666',
            fontSize: 10
          },
          axisLine: {
            show: false
          },
          splitLine: {
            lineStyle: {
              type: 'dashed',
              color: '#f1f1f2'
            }
          }
        },
        series: [{
          data: this.chartData.fieldData.value,
          type: 'line',
          smooth: true,
          symbolSize: 1,
          lineStyle: {
            color: '#0092ff'
          },
          areaStyle: {
            color: '#0092ff',
            opacity: 0.1
          }
        }]
      };
      const dataVolumeNumUnit = this.chartData.dataVolume.dataVolumeNum.unitName;
      const dataIncrementNumUnit = this.chartData.dataVolume.dataIncrementNum.unitName;
      this.dataVolumeChart = {
        color: ['#0092ff', '#ffbd1b'],
        grid: {
          top: 40,
          left: 40,
          right: 40,
          bottom: 50
        },
        tooltip: {
          trigger: 'axis',
          backgroundColor: '#fff',
          textStyle: {
            color: '#666',
            fontSize: 12
          },
          axisPointer: {
            lineStyle: {
              color: '#dcdcdc'
            }
          },
          formatter: (params) => {
            if (params.length) {
              let tootipStr = `${params[0].axisValue}`;
              params.forEach((item, index) => {
                tootipStr += `<br/>${params[index].marker}${params[index].data.name}：${params[index].value} ${params[index].data.unit}`;
              });
              return tootipStr;
            }
            return '';
          }
        },
        legend: {
          data: [
            {
              name: `数据量（${dataVolumeNumUnit}）`,
              icon: 'circle'
            },
            {
              name: `数据增量（${dataIncrementNumUnit}）`,
              icon: 'circle'
            }
          ]
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: this.chartData.dataVolume.date,
          axisTick: {
            show: false
          },
          axisLabel: {
            color: '#666',
            fontSize: 12
          },
          axisLine: {
            lineStyle: {
              color: '#ddd'
            }
          }
        },
        yAxis: [
          {
            name: '数据量',
            type: 'value',
            axisTick: {
              show: false
            },
            axisLabel: {
              color: '#666',
              fontSize: 10
            },
            axisLine: {
              show: false
            },
            splitLine: {
              show: false,
              lineStyle: {
                type: 'dashed',
                color: '#f1f1f2'
              }
            }
          },
          {
            name: '数据增量',
            type: 'value',
            axisTick: {
              show: false
            },
            axisLabel: {
              color: '#666',
              fontSize: 10
            },
            axisLine: {
              show: false
            },
            splitLine: {
              show: false,
              lineStyle: {
                type: 'dashed',
                color: '#f1f1f2'
              }
            }
          }
        ],
        series: [
          {
            name: `数据量（${dataVolumeNumUnit}）`,
            type: 'line',
            smooth: true,
            symbolSize: 1,
            lineStyle: {
              color: '#0092ff'
            },
            areaStyle: {
              color: '#0092ff',
              opacity: 0.1
            },
            data: this.chartData.dataVolume.dataVolumeNum.data.map(item => {
              return {
                name: '数据量',
                unit: dataVolumeNumUnit,
                value: item
              };
            })
          },
          {
            name: `数据增量（${dataIncrementNumUnit}）`,
            yAxisIndex: 1,
            type: 'line',
            smooth: true,
            symbolSize: 1,
            lineStyle: {
              color: '#ffbd1b'
            },
            areaStyle: {
              color: '#ffbd1b',
              opacity: 0.1
            },
            data: this.chartData.dataVolume.dataIncrementNum.data.map(item => {
              return {
                name: '数据增量',
                unit: dataIncrementNumUnit,
                value: item
              };
            })
          }
        ]
      };
    }
  }
};
</script>




