
<template>
  <div class="metadata-management-field-info">
    <div
      v-show="!showDetail"
      class="field-list"
    >
      <div>
        <div class="pull-right">
          <gs-button
            v-if="!tableInfo.virtualTable && tableInfo.status !== 'DELETED'"
            :style="{visibility: !isEdit ? 'visible' : 'hidden'}"
            @click="edit(true)"
          >编辑</gs-button>
        </div>
        <gs-form
          ref="queryForm"
          inline
        >
          <gs-form-item
            label="名称"
            prop="columnName"
          >
            <gs-input
              v-model="params.columnName"
              placeholder="请输入名称"
            />
          </gs-form-item>

          <gs-form-item
            label="类型"
            prop="params.type"
          >
            <gs-select v-model="params.type">
              <gs-option
                v-for="item in options"
                :key="item.value"
                :label="item.name"
                :value="item.value"
              />
            </gs-select>
          </gs-form-item>

          <gs-form-item
            label="字典内容"
            prop="content"
          >
            <gs-input
              v-model="params.content"
              placeholder="请输入字典内容"
            />
          </gs-form-item>

          <gs-form-item>
            <gs-button
              type="primary"
              @click="getColumnInfo"
            >查询</gs-button>
          </gs-form-item>
        </gs-form>
      </div>
      <div
        v-loading="isLoading"
        class="relative"
      >
        <h3>
          <i
            :style="{transform: packUp1 ? 'rotate(-90deg)' : 'rotate(0deg)'}"
            class="gs-icon-down"
            @click="packUp1=!packUp1"
          />
          <span>普通字段</span>
        </h3>
        <gs-collapse-transition>
          <div v-show="!packUp1">
            <el-table
              :data="normalTable.data"
              stripe
              max-height="300"
            >
              <el-table-column
                show-overflow-tooltip
                prop="columnName"
                label="名称"
              />
              <el-table-column
                show-overflow-tooltip
                prop="type"
                label="类型"
              />
              <el-table-column
                show-overflow-tooltip
                label="描述"
              >
                <template slot-scope="scope">
                  <span v-if="!isEdit">{{ scope.row.description }}</span>
                  <gs-input
                    v-else
                    v-model="scope.row.editDescription"
                    placeholder="请输入字段描述"
                  />
                </template>
              </el-table-column>
              <!-- <el-table-column
                align="center"
                label="字段画像"
                width="80"
              >
                <template slot-scope="scope">
                  <gs-button
                    type="text-primary"
                    @click="toDetail(scope.row.tableId)"
                  >画像详情</gs-button>
                </template>
              </el-table-column> -->
            </el-table>
          </div>
        </gs-collapse-transition>
        <h3>
          <i
            :style="{transform: packUp2 ? 'rotate(-90deg)' : 'rotate(0deg)'}"
            class="gs-icon-down"
            @click="packUp2=!packUp2"
          />
          <span>分区字段</span>
        </h3>
        <gs-collapse-transition>
          <div v-show="!packUp2">
            <el-table
              :data="partitionTable.data"
              stripe
              max-height="300"
            >
              <el-table-column
                show-overflow-tooltip
                prop="columnName"
                label="名称"
              />
              <el-table-column
                show-overflow-tooltip
                prop="type"
                label="类型"
              />
              <el-table-column
                show-overflow-tooltip
                label="描述"
              >
                <template slot-scope="scope">
                  <span v-if="!isEdit">{{ scope.row.description }}</span>
                  <gs-input
                    v-else
                    v-model="scope.row.editDescription"
                    placeholder="请输入描述"
                  />
                </template>
              </el-table-column>
              <!-- <el-table-column
                align="center"
                label="字段画像"
                width="80"
              >
                <template slot-scope="scope">
                  <gs-button
                    type="text-primary"
                    @click="toDetail(scope.row.tableId)"
                  >画像详情</gs-button>
                </template>
              </el-table-column> -->
            </el-table>
          </div>
        </gs-collapse-transition>
        <div
          v-show="isEdit"
          class="action-btns"
        >
          <action-comfirm
            title="确定修改吗？"
            @comfirm="updateColumnInfo"
          >
            <gs-button
              slot="reference"
              type="primary"
            >确定
            </gs-button>
          </action-comfirm>
          <gs-button
            @click="edit(false)"
          >取消</gs-button>
        </div>
      </div>
    </div>
    <field-info-detail
      v-if="showDetail"
      ref="field-info-detail"
    />
  </div>
</template>

<script>
import './field-info.scss';
import FieldInfoDetail from './field-info-detail.vue';
import * as fieldInfoService from 'src/service/metadata-management/field-info';
import ActionComfirm from 'src/components/action-comfirm';
export default {
  components: {
    FieldInfoDetail,
    ActionComfirm
  },
  props: {
    tableInfo: {
      type: Object,
      default() {
        return {};
      }
    }
  },
  data() {
    return {
      isLoading: false,
      isEdit: false,
      showDetail: false,
      query: {
      },
      params: {
        columnName: '',
        type: -1,
        content: ''
      },
      packUp1: false,
      packUp2: false,
      options: [
        {
          name: '全部',
          value: -1
        },
        {
          name: 'tinyint',
          value: 'tinyint'
        },
        {
          name: 'smallint',
          value: 'smallint'
        },
        {
          name: 'integer',
          value: 'integer'
        },
        {
          name: 'int',
          value: 'int'
        },
        {
          name: 'bigint',
          value: 'bigint'
        },
        {
          name: 'float',
          value: 'float'
        },
        {
          name: 'double',
          value: 'double'
        },
        {
          name: 'double precision',
          value: 'double precision'
        },
        {
          name: 'decimal',
          value: 'decimal'
        },
        {
          name: 'numeric',
          value: 'numeric'
        },
        {
          name: 'timestamp',
          value: 'timestamp'
        },
        {
          name: 'date',
          value: 'date'
        },
        {
          name: 'interval',
          value: 'interval'
        },
        {
          name: 'string',
          value: 'string'
        },
        {
          name: 'varchar',
          value: 'varchar'
        },
        {
          name: 'char',
          value: 'char'
        },
        {
          name: 'boolean',
          value: 'boolean'
        },
        {
          name: 'binary',
          value: 'binary'
        },
        {
          name: 'array',
          value: 'array'
        },
        {
          name: 'map',
          value: 'map'
        },
        {
          name: 'struct',
          value: 'struct'
        },
        {
          name: 'union',
          value: 'union'
        }
      ],
      tableList: [],
      // 普通字段
      normalTable: {
        data: []
      },
      // 分区字段
      partitionTable: {
        data: []
      }
    };
  },
  mounted() {
    this.getColumnInfo();
  },
  methods: {
    /**
     * 编辑
     */
    edit(flag) {
      this.isEdit = flag;
    },
    /**
     * 跳转到详情界面
     */
    toDetail(id) {
      // todo
      this.showDetail = true;
      this.$nextTick(() => {
        this.$refs['field-info-detail'].show(id);
      });
    },
    updateColumnInfo() {
      const saveDescriptions = this.tableList.filter(item => {
        return item.editDescription !== item.description;
      }).map(item => {
        return {
          tableId: this.$route.params.id,
          type: item.type,
          columnName: item.columnName,
          description: item.editDescription
        };
      });
      if (saveDescriptions.length === 0) {
        this.$Message.warning('请至少修改一条记录');
        return;
      }
      this.isLoading = true;
      fieldInfoService.updateColumnInfo(saveDescriptions).then((res) => {
        this.isLoading = false;
        this.edit(false);
        // 由于后端修改后，无法及时修改查询的结果，所以只能先前端修改显示了
        this.tableList.filter(item => {
          return item.editDescription !== item.description;
        }).forEach(item => {
          item.description = item.editDescription;
        });
        // this.getColumnInfo();
        this.$Message.success('成功修改字段信息');
      }).catch((res) => {
        this.isLoading = false;
      });
    },
    /**
     * 查询表详情列表
     */
    getColumnInfo() {
      this.isLoading = true;
      fieldInfoService.getColumnInfo({
        guid: this.$route.params.id,
        columnFilter: {
          columnName: this.params.columnName,
          type: this.params.type === -1 ? '' : this.params.type,
          description: this.params.content
        }
      }).then((res) => {
        this.isLoading = false;
        this.partitionTable.data = [];
        this.normalTable.data = [];
        this.tableList = res;
        res.forEach(item => {
          item.editDescription = item.description;
          // 判断是否为分区字段
          if (item.partitionKey) {
            this.partitionTable.data.push(item);
          } else {
            this.normalTable.data.push(item);
          }
        });
      }).catch((res) => {
        this.isLoading = false;
      });
    }
  }
};
</script>




