<template>
  <div
    v-loading="isLoading"
    class="metadata-management-table-info relative"
  >
    <div class="text-right">
      <gs-button
        v-if="!tableInfo.virtualTable && tableInfo.status !== 'DELETED'"
        :style="{visibility: !isEdit ? 'visible' : 'hidden'}"
        @click="edit(true)"
      >编辑</gs-button>
    </div>
    <gs-form
      ref="form"
      label-width="95px"
    >
      <h3>
        <i
          :style="{transform: packUp1 ? 'rotate(-90deg)' : 'rotate(0deg)'}"
          class="gs-icon-down"
          @click="packUp1=!packUp1"
        />
        <span>基础信息</span>
      </h3>
      <gs-collapse-transition>
        <div v-show="!packUp1">
          <gs-form-item
            label="表名称："
          >
            <span>{{ tableInfo.showTableName }}</span>
          </gs-form-item>
          <gs-form-item
            label="所属业务："
          >
            <span>{{ tableInfo.business }}</span>
          </gs-form-item>
          <gs-form-item
            label="表关联信息："
          >
            <div
              v-for="(relation, index) in tableInfo.relations"
              :key="index"
            >{{ relation }}</div>
          </gs-form-item>
          <gs-form-item
            label="创建人："
          >
            <span>{{ tableInfo.owner }}</span>
          </gs-form-item>
          <gs-form-item
            label="创建时间："
          >
            <span>{{ tableInfo.createTime }}</span>
          </gs-form-item>
          <gs-form-item
            label="类别："
          >
            <span>{{ tableInfo.category }}</span>
          </gs-form-item>
          <gs-form-item
            label="所属数据库："
          >
            <i
              class="iconfont icon-yuanshuju"
            />
            <span>{{ tableInfo.databaseName }}</span>
          </gs-form-item>
          <gs-form-item
            label="类型："
          >
            <span>{{ tableInfo.type }}</span>
          </gs-form-item>
          <gs-form-item
            label="表生命周期："
          >
            <span>{{ tableInfo.tableLife }}</span>
          </gs-form-item>
          <gs-form-item
            label="分区表："
          >
            <span>{{ tableInfo.partitionTable ? '是' : '否' }}</span>
          </gs-form-item>
          <gs-form-item
            label="分区生命周期："
          >
            <span>{{ tableInfo.partitionLife }}</span>
          </gs-form-item>
          <gs-form-item
            label="格式："
          >
            <span>{{ tableInfo.format }}</span>
          </gs-form-item>
          <gs-form-item
            label="位置："
          >
            <span>{{ tableInfo.location }}</span>
          </gs-form-item>
          <gs-form-item
            ref="description"
            label="描述："
          >
            <span v-if="!isEdit">{{ tableInfo.description }}</span>
            <gs-input
              v-else
              v-model="description"
              placeholder="请输入描述"
            />
          </gs-form-item>
        </div>
      </gs-collapse-transition>
      <h3>
        <i
          :style="{transform: packUp2 ? 'rotate(-90deg)' : 'rotate(0deg)'}"
          class="gs-icon-down"
          @click="packUp2=!packUp2"
        />
        <span>分类信息</span>
      </h3>
      <gs-collapse-transition>
        <div v-show="!packUp2">
          <gs-form-item
            label="所属主题："
            prop="database"
          >
            <span>{{ tableInfo.topic }}</span>
          </gs-form-item>
        </div>
      </gs-collapse-transition>
      <!-- <h3>
        <i
          :style="{transform: packUp3 ? 'rotate(-90deg)' : 'rotate(0deg)'}"
          class="gs-icon-down"
          @click="packUp3=!packUp3"
        />
        <span>版权信息</span>
      </h3>
      <gs-collapse-transition>
        <div v-show="!packUp3">
          <gs-form-item
            label="权限状态："
            prop="database"
          >
            <gs-checkbox v-model="checked1">读写</gs-checkbox>
            <gs-checkbox v-model="checked2">改表</gs-checkbox>
            <gs-checkbox v-model="checked1">删表</gs-checkbox>
          </gs-form-item>
        </div>
      </gs-collapse-transition> -->
    </gs-form>
    <div
      v-show="isEdit"
      class="action-btns"
    >
      <action-comfirm
        title="确定修改吗？"
        @comfirm="updateTableInfo"
      >
        <gs-button
          slot="reference"
          type="primary"
        >确定
        </gs-button>
      </action-comfirm>
      <gs-button
        @click="edit(false)"
      >取消</gs-button>
    </div>
  </div>
</template>

<script>
import './table-info.scss';
import * as tableInfoService from 'src/service/metadata-management/table-info';
import ActionComfirm from 'src/components/action-comfirm';
export default {
  components: {
    ActionComfirm
  },
  props: {
    tableInfo: {
      type: Object,
      default() {
        return {};
      }
    }
  },
  data() {
    return {
      isLoading: false,
      isEdit: false,
      packUp1: false,
      packUp2: false,
      // packUp3: false,
      description: ''
    };
  },
  methods: {
    edit(flag) {
      this.isEdit = flag;
      if (flag) {
        this.description = this.tableInfo.description;
        this.$refs['description'].$el.scrollIntoView();
      }
    },
    updateTableInfo() {
      if (this.description === this.tableInfo.description) {
        this.$Message.warning('暂无需要保存数据');
        return;
      }
      this.isLoading = true;
      tableInfoService.updateTableInfo({
        guid: this.$route.params.id,
        description: this.description
      }).then((res) => {
        this.isLoading = false;
        this.edit(false);
        // 由于后端修改后，无法及时修改查询的结果，所以只能先前端修改显示了
        this.tableInfo.description = this.description;
        // this.$emit('refreshTableInfo');
        this.$Message.success('成功修改表信息');
      }).catch((res) => {
        this.isLoading = false;
      });
    }
  }
};
</script>




