
<template>
  <div
    v-loading="loading"
    class="metadata-management-table-sql"
  >
    <codemirror
      :value="tableSql"
      :options="cmOptions"
    />
  </div>
</template>

<script>
import {
  mapState,
  mapActions
} from 'vuex';
import {
  checkLoading
} from 'src/store/status';
import './style';

export default {
  data() {
    return {
      cmOptions: {
        readOnly: 'nocursor',
        tabSize: 4,
        mode: 'text/x-hive',
        styleActiveLine: true,
        lineNumbers: true,
        line: true
      }
    };
  },

  computed: {
    ...mapState('table', {
      tableSql: 'tableSql',
      tableSqlStatus: 'tableSqlStatus'
    }),
    loading() {
      return checkLoading(this.tableSqlStatus);
    }
  },

  mounted() {
    this.getTableSql(this.$route.params.id);
  },

  methods: {
    ...mapActions('table', [
      'getTableSql'
    ])
  }
};
</script>




