
<template>
  <div class="metadata-management-table-relation clearfix">
    <div
      class="svg-wrap"
    >
      <div
        class="relative svg-content"
      >
        <table-relation-chart
          ref="tableRelationChart"
          :current-selected="currentSelected.guid"
          :guid="guid"
          :relation-filter="relationFilter"
          @selectChange="changeSelectItem"
        />
      </div>
    </div>
    <div
      class="relation-ship-detail pull-right"
    >
      <div
        v-loading="isDetailLoading"
        class="relative"
      >
        <div class="text-center tab-relation-filter">
          <el-radio-group v-model="tabPosition">
            <el-radio-button label="relationshipProp">关系图属性</el-radio-button>
            <el-radio-button label="tableDetail">表详情</el-radio-button>
          </el-radio-group>
          <gs-button
            :disabled="viewRelationship"
            type="text-primary"
            class="relationship-btn"
            @click="loadSelectRelationship"
          >血缘图</gs-button>
        </div>
        <div
          v-if="tabPosition === 'relationshipProp'"
          class="relationship-prop"
        >
          <div class="relationship-item">
            <h4>显示操作</h4>
            <span>方向</span>
            <el-radio
              v-model="relationFilter.direction"
              label="BOTH"
            >上下游双向</el-radio>
            <el-radio
              v-model="relationFilter.direction"
              label="INPUT"
            >仅上游</el-radio>
            <el-radio
              v-model="relationFilter.direction"
              label="OUTPUT"
            >仅下游</el-radio>
          </div>
          <div class="relationship-item">
            <h4>深度</h4>
            <number-slider
              :limit-number="relationFilter.limitNumber"
              v-model="relationFilter.selectedDepth"
              @change="selectedDepthChange"
            />
          </div>
        </div>
        <div v-else>
          <div class="relationship-item">
            <h4>表名称</h4>
            <i
              class="iconfont icon-biaoge"
            />
            <span>{{ detailInfo.tableName }}</span>
          </div>
          <div class="relationship-item">
            <h4>所属数据库</h4>
            <i
              class="iconfont icon-yuanshuju"
            />
            <span>{{ detailInfo.dbName }}</span>
          </div>
          <div class="relationship-item">
            <h4>直接上游表数量</h4>
            <span>{{ detailInfo.directUpStreamNum }}</span>
          </div>
          <div class="relationship-item">
            <h4>直接下游表数量</h4>
            <span>{{ detailInfo.directDownStreamNum }}</span>
          </div>
          <div class="relationship-item">
            <h4>上游表层数</h4>
            <span>{{ detailInfo.upStreamLevelNum }}</span>
          </div>
          <div class="relationship-item">
            <h4>下游表层数</h4>
            <span>{{ detailInfo.downStreamLevelNum }}</span>
          </div>
          <div class="relationship-item">
            <h4>表结构更新时间（DDL）</h4>
            <span>{{ detailInfo.updateTime }}</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import './tableRelation.scss';
import * as relationshipService from 'src/service/metadata-management/relationship';
import TableRelationChart from './tableRelationChart.vue';
import NumberSlider from 'src/components/number-slider';
export default {
  components: {
    TableRelationChart,
    NumberSlider
  },
  data() {
    return {
      tabPosition: 'relationshipProp',
      isDetailLoading: false,
      detailInfo: {},
      data: [],
      relationFilter: {
        direction: 'BOTH',
        selectedDepth: 2,
        limitNumber: 6
      },
      currentSelected: {
        guid: this.$route.params.id
      }
    };
  },
  computed: {
    viewRelationship() {
      return this.$route.params.id === this.currentSelected.guid;
    },
    guid() {
      return this.$route.params.id;
    }
  },
  watch: {
    'relationFilter.direction'(value) {
      this.$refs['tableRelationChart'].getTableLineage();
    }
  },
  mounted() {
    this.getData();
  },
  methods: {
    /**
     * 获取页面数据
     */
    getData() {
      this.getTableLineageInfo();
    },
    /**
     * 修改当前选中元素
     */
    changeSelectItem(id) {
      this.currentSelected.guid = id;
      this.getTableLineageInfo();
    },
    /**
     * 加载选中元素的表血缘关系
     */
    loadSelectRelationship() {
      this.$router.replace({
        path: `/main/metadata/detail/${this.currentSelected.guid}`,
        query: {
          module: 'relationship'
        }
      }, () => {
        this.$refs['tableRelationChart'].getTableLineage();
        this.$emit('refreshTableInfo');
      });
    },
    /**
     * 获取表血缘详情
     */
    getTableLineageInfo() {
      this.isDetailLoading = true;
      const id = this.currentSelected.guid;
      relationshipService.getTableLineageInfo(this.currentSelected.guid).then((res) => {
        // 重复请求处理
        if (id !== this.currentSelected.guid) {
          return;
        }
        this.isDetailLoading = false;
        this.detailInfo = res;
      }).catch((res) => {
        this.isDetailLoading = false;
      });
    },
    /**
     * 关系图属性深度改变事件
     */
    selectedDepthChange(value) {
      this.$refs['tableRelationChart'].getTableLineage();
    }
  }
};
</script>



