
<template>
  <div class="metadata-management-relationship clearfix">
    <div class="tab-relation">
      <el-radio-group v-model="showRelation">
        <el-radio-button label="tableRelation">表血缘</el-radio-button>
        <el-radio-button label="fieldRelation">字段血缘</el-radio-button>
      </el-radio-group>
    </div>
    <div class="relationship-content">
      <table-relation
        v-if="showRelation === 'tableRelation'"
        @refreshTableInfo="refreshTableInfo"
      />
      <field-relation
        v-if="showRelation === 'fieldRelation'"
        @refreshTableInfo="refreshTableInfo"
      />
    </div>
  </div>
</template>

<script>
import './relationship.scss';
import TableRelation from './tableRelation.vue';
import FieldRelation from './fieldRelation.vue';
import * as d3Selection from 'd3-selection';
export default {
  components: {
    TableRelation,
    FieldRelation
  },
  data() {
    return {
      showRelation: 'tableRelation'
    };
  },
  mounted() {
  },
  destroyed() {
    d3Selection.selectAll('.d3-tip').remove();
  },
  methods: {
    /**
     * 刷新当前表信息
     */
    refreshTableInfo() {
      this.$emit('refreshTableInfo');
    },
    /**
     * 获取页面数据
     */
    getData() {
    }
  }
};
</script>




