
<template>
  <div class="metadata-management-statistics-history-list">
    <el-table
      :data="dataList"
      stripe
    >
      <el-table-column
        show-overflow-tooltip
        prop="date"
        label="日期"
        width="175"
      />
      <el-table-column
        align="center"
        show-overflow-tooltip
        prop="fieldNum"
        label="字段数量"
      />
      <el-table-column
        align="center"
        show-overflow-tooltip
        prop="fileNum"
        label="文件个数"
      />
      <el-table-column
        align="center"
        show-overflow-tooltip
        prop="dataVolume"
        label="数据量"
      />
      <el-table-column
        align="center"
        show-overflow-tooltip
        prop="dataIncrement"
        label="数据增量"
      />
      <!-- <el-table-column
        align="center"
        label="数据来源"
        min-width="240"
        show-overflow-tooltip
      >
        <template slot-scope="scope">
          <div
            v-for="(source, index) in scope.row.sourceTable"
            :key="index"
            class="text-overflow data-source"
          >
            <span style="margin-left: 4px">{{ source.database }}</span>
            <i
              :style="{transform: 'rotate(-90deg)'}"
              class="gs-icon-down"
            />
            <a @click="toDetail(source.tableId)">{{ source.tableName }}</a>
          </div>
        </template>
      </el-table-column> -->
    </el-table>
    <gs-pagination
      v-show="total>0"
      :current="table.currentPage"
      :total="total"
      :page-size="table.pageSize"
      :page-sizes="table.pageSizes"
      layout="range,total,pagesizes,pager,jumper,jumpbtn"
      @page-change="pageChange"
      @page-size-change="pageSizeChange"
    />
  </div>
</template>

<script>
import './table-list.scss';

export default {
  props: {
    dataList: {
      type: Array,
      default() {
        return [];
      }
    },
    total: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      // 普通字段
      table: {
        currentPage: 1,
        pageSize: 10,
        pageSizes: [10, 20, 30, 40, 50, 100]
      }
    };
  },

  methods: {
    /**
     * 跳转到表详情
     */
    toDetail(id) {
      this.$router.push({
        path: `/main/metadata/detail/${id}`,
        query: {
          module: 'table'
        }
      });
    },
    getPaginationParam() {
      return {
        offset: (this.table.currentPage - 1) * this.table.pageSize,
        limit: this.table.pageSize
      };
    },

    /**
     * 当前页改变事件
     */
    pageChange(newPage) {
      this.table.currentPage = newPage;
      this.$emit('search');
    },
    /**
     * 每页显示条数改变事件
     */
    pageSizeChange(newPageSize) {
      this.table.pageSize = newPageSize;
      this.table.currentPage = 1;
      this.$emit('search');
    }
  }
};
</script>




