import http from 'src/utils/http';

export function getTableStatToday(id) {
  return http.get(`table/stat/today/${id}`);
}

export function getTableStatHistory(param) {
  return http.post('table/stat/history', param);
}

export function getTableSourceTableCount(param) {
  return http.post('table/stat/sourceTableCount', param);
}



