
<template>
  <div class="metadata-management-statistics-history">
    <div>
      <gs-form
        ref="queryForm"
        inline
      >
        <gs-form-item>
          <gs-button
            @click="goback"
          >返回上一级</gs-button>
        </gs-form-item>
        <gs-form-item
          label=""
          prop="filename"
        >
          <gs-date-picker
            :append-to-body="true"
            :clearable="false"
            v-model="dateRange.value"
            :disabled-date="disabledDate"
            type="date-range"
          />
        </gs-form-item>
        <gs-form-item>
          <gs-button
            type="primary"
            @click="search"
          >查询</gs-button>
        </gs-form-item>
      </gs-form>
    </div>
    <div class="tab-content clearfix">
      <el-radio-group
        v-model="tabPosition"
        class="pull-left"
      >
        <el-radio-button label="1">日</el-radio-button>
        <el-radio-button label="2">月</el-radio-button>
        <el-radio-button label="3">年</el-radio-button>
      </el-radio-group>
      <el-radio-group
        v-model="activeName"
        class="pull-right"
      >
        <el-radio-button label="tableList">表格展示</el-radio-button>
        <el-radio-button label="trend">走势图</el-radio-button>
      </el-radio-group>
    </div>
    <div
      v-loading="isLoading"
      class="relative"
    >
      <table-list
        v-show="activeName === 'tableList'"
        ref="table-list"
        :data-list="dataList"
        :total="total"
        @search="search"
      />
      <trend-chart
        v-show="activeName === 'trend'"
        :list-length="dataList.length"
        :chart-data="chartData"
      />
    </div>
  </div>
</template>

<script>
import './history.scss';
import _ from 'lodash';
import moment from 'moment';
import TableList from './table-list.vue';
import TrendChart from './trend-chart.vue';
import * as statisticsService from 'src/service/metadata-management/statistics';
export default {
  components: {
    TableList,
    TrendChart
  },
  data() {
    return {
      isLoading: false,
      param: {},
      activeName: 'tableList',
      tabPosition: '1',
      dataList: [],
      chartData: {
        fieldData: {},
        dataVolume: {}
        // sourceCount: {}
      },
      total: 0,
      // 时间选择的限制
      disabledDate(date) {
        return date.isAfter(moment().subtract(0, 'day'));
      },
      dateRange: {
        value: [moment().subtract(1, 'week'), moment()]
      }
    };
  },
  watch: {
    tabPosition: function (newQuestion, oldQuestion) {
      this.search();
    }
  },
  mounted() {
    this.search();
  },
  methods: {
    /**
     * 查询
     */
    search() {
      const paginationParam = this.$refs['table-list'].getPaginationParam();
      const formatDir = {
        '1': 'YYYY-MM-DD',
        '2': 'YYYY-MM',
        '3': 'YYYY'
      };
      const param = {
        'tableId': this.$route.params.id,
        'dateType': this.tabPosition,
        'fromDate': this.dateRange.value[0].format(formatDir[this.tabPosition]),
        'endDate': this.dateRange.value[1].format(formatDir[this.tabPosition]),
        ...paginationParam
      };
      this.param = param;
      this.isLoading = true;
      Promise.all([this.getTableStatHistory(param)])
        .then((res) => {
          // 防止多次请求时，先请求后回来
          if (!_.isEqual(param, this.param)) {
            return;
          }
          this.isLoading = false;
          const historyRes = res[0];
          // const countRes = res[1];

          this.dataList = historyRes.list;
          this.total = historyRes.total;

          this.setChartData(historyRes.list);
        })
        .catch(() => {
          this.isLoading = false;
        });
    },
    /**
     * 设置走势图表数据
     */
    setChartData(historyData, countData) {
      // this.chartData.sourceCount = {
      //   legend: _.map(countData, 'tableName'),
      //   data: _.map(countData, item => {
      //     return {
      //       name: item.tableName,
      //       value: item.count
      //     };
      //   })
      // };
      this.chartData.fieldData = {
        date: _.map(historyData, 'date'),
        value: _.map(historyData, 'fieldNum')
      };
      this.chartData.dataVolume = {
        date: _.map(historyData, 'date'),
        dataVolumeNum: {
          unitName: historyData.length > 0 ? historyData[0].dataVolumeNumUnit : '',
          data: _.map(historyData, 'dataVolumeNum')
        },
        dataIncrementNum: {
          unitName: historyData.length > 0 ? historyData[0].dataIncrementNumUnit : '',
          data: _.map(historyData, 'dataIncrementNum')
        }
      };
    },
    /**
     * 获取列表数据
     */
    getTableStatHistory(params) {
      return statisticsService.getTableStatHistory(params);
    },
    /**
     * 获取数据来源表个数统计
     */
    getSourceTableCount(params) {
      return statisticsService.getTableSourceTableCount(params);
    },
    /**
     * 返回上一级
     */
    goback() {
      this.$emit('back');
    },
    /**
     * 查看历史信息
     */
    viewHistory() {
    }
  }
};
</script>




