import http from 'src/utils/http';

export function getTableLineage(id, param) {
  return http.get(`metadata/table/lineage/${id}`, { params: param });
}

export function getTableLineageInfo(id) {
  return http.get(`metadata/table/lineage/depth/${id}`);
}

export function getColumnLineage(id, param) {
  return http.get(`metadata/column/lineage/${id}`, { params: param });
}

export function getColumnLineageInfo(id) {
  return http.get(`metadata/column/lineage/depth/${id}`);
}




