<template>
  <div class="metadata-management-detail">
    <div class="content-header">
      <bread-crumb
        :current-level="'表详情'"
        upper-level="元数据管理"
        back-path="/main/metadata?back"
      />
    </div>
    <div class="content-body">
      <div class="table-name">
        <span
          v-if="!tableInfo.showTableName"
          class="show-visable"
        >-</span>
        <span>{{ tableInfo.showTableName }}</span>
        <!-- <a
          @click="refreshTableInfo"
        >
          <i
            :class="{'animation-rotate': isLoading}"
            class="iconfont icon-shuaxin2"
          />
        </a> -->
      </div>
      <gs-tabs
        v-model="activeName"
        type="content-tab"
      >
        <gs-tab-pane
          label="表信息"
          name="table"
        >
          <table-info
            v-loading="isLoading"
            v-if="activeName === 'table'"
            :table-info="tableInfo"
            @refreshTableInfo="getTableInfo"
          />
        </gs-tab-pane>
        <gs-tab-pane
          label="字段信息"
          name="field"
        >
          <field-info
            v-if="activeName === 'field'"
            :table-info="tableInfo"
          />
        </gs-tab-pane>
        <gs-tab-pane
          v-if="!tableInfo.virtualTable && tableInfo.status !== 'DELETED'"
          :disabled="isLoading"
          label="数据概览"
          name="overview"
        >
          <overview v-if="activeName === 'overview'" />
        </gs-tab-pane>
        <gs-tab-pane
          label="数据血缘"
          name="relationship"
        >
          <relationship
            v-if="activeName === 'relationship'"
            @refreshTableInfo="getTableInfo"
          />
        </gs-tab-pane>
        <gs-tab-pane
          v-if="!tableInfo.virtualTable && tableInfo.status !== 'DELETED'"
          :disabled="isLoading"
          label="统计信息"
          name="statistics"
        >
          <statistics
            v-if="activeName === 'statistics'"
            @refreshTableInfo="getTableInfo"
          />
        </gs-tab-pane>
        <gs-tab-pane
          v-if="!tableInfo.virtualTable && tableInfo.status !== 'DELETED'"
          :disabled="isLoading"
          label="建表语句"
          name="tableSql"
        >
          <table-sql v-if="activeName === 'tableSql'" />
        </gs-tab-pane>
      </gs-tabs>
    </div>
  </div>
</template>

<script>
import TableInfo from './table-info/table-info.vue';
import FieldInfo from './field-info/field-info.vue';
import Overview from './overview/overview.vue';
import Relationship from './relationship/relationship.vue';
import Statistics from './statistics/statistics.vue';
import TableSql from './table-sql/table-sql.vue';
import * as tableInfoService from 'src/service/metadata-management/table-info';
import * as cacheService from 'src/service/cache';
import BreadCrumb from 'src/components/breadcrumb';
import './style';
export default {
  components: {
    TableInfo,
    FieldInfo,
    Overview,
    Relationship,
    Statistics,
    TableSql,
    BreadCrumb
  },

  data() {
    return {
      isLoading: false,
      activeName: 'table',
      tableInfo: {}
    };
  },
  watch: {
    activeName(value, oldvalue) {
      this.$router.replace({
        query: {
          module: value
        }
      });
    }
  },
  beforeRouteUpdate(to, from, next) {
    this.activeName = to.query.module;
    next();
  },
  beforeMount() {
    const module = this.$route.query.module;
    if (module) {
      this.activeName = module;
    };
  },
  /**
   * 路由离开触发
   */
  beforeRouteLeave(to, from, next) {
    // 判断点击返回元数据列表时，进入缓存界面
    if (to.path === '/main/metadata' && to.query.back === null) {
      to.meta.keepAlive = true;
    }
    next();
  },
  mounted() {
    this.getTableInfo();
  },
  methods: {
    /**
     * 刷新表详情
     */
    refreshTableInfo() {
      if (this.isLoading) {
        return;
      }
      this.isLoading = true;
      cacheService.refreshDataCache().then((res) => {
        this.getTableInfo();
      }).catch((res) => {
        this.isLoading = false;
      });
    },
    /**
     * 查询表详情列表
     */
    getTableInfo() {
      this.isLoading = true;
      tableInfoService.getTableInfo(this.$route.params.id).then((res) => {
        this.isLoading = false;
        this.tableInfo = res;
        this.tableInfo.showTableName = `${this.tableInfo.tableName}${this.tableInfo.virtualTable ? '(虚拟表)' : ''}`;
      }).catch((res) => {
        this.isLoading = false;
      });
    }
  }
};
</script>




