<template>
  <div
    v-loading="isSvgLoading"
    class="table-relation-chart"
  >
    <svg
      ref="table-svg"
      width="100%"
      height="100%"
      class="table-svg"
    >
      <text
        v-if="!isSvgLoading && lineageData && !lineageData.relations.length"
        x="50%"
        y="50%"
        alignment-baseline="middle"
        text-anchor="middle"
      >没有表血缘数据</text>
      <g ref="g" />
    </svg>
  </div>
</template>

<script>
import './tableRelationChart.scss';
import * as relationshipService from 'src/service/metadata-management/relationship';
import $ from 'jquery';
import _ from 'lodash';
import platform from 'platform';
import dagreD3 from 'dagre-d3';
import * as d3Selection from 'd3-selection';
import * as d3Zoom from 'd3-zoom';
import d3Tip from 'd3-tip';
const d3 = Object.assign({}, d3Selection, d3Zoom);
export default {
  props: {
    // 当前选中的guid
    currentSelected: {
      type: String,
      default: ''
    },
    // 当前的guid
    guid: {
      type: String,
      default: ''
    },
    relationFilter: {
      type: Object,
      default() {
        return {};
      }
    }
  },
  data() {
    return {
      isSvgLoading: false,
      param: {},
      fromToObj: {},
      // 返回的表血缘数据
      lineageData: null,
      // 节点状态
      entityStateReadOnly: {
        ACTIVE: false,
        DELETED: true,
        STATUS_ACTIVE: false,
        STATUS_DELETED: true
      },
      activeTip: false,
      g: null,
      zoom: null
    };
  },
  mounted() {
    if (platform.name === 'IE') {
      $('.table-relation-chart').css('opacity', '0');
    }
    this.zoom = d3.zoom().scaleExtent([0.5, 6]);
    d3.select(this.$refs['table-svg']).call(this.zoom.on('zoom', this.zoomed));
    this.getTableLineage();
  },
  destroyed() {
    d3.select('.d3-tip').remove();
  },
  methods: {
    /**
     * 获取表血缘关系
     */
    getTableLineage() {
      this.fromToObj = {};
      this.isSvgLoading = true;
      const param = {
        depth: this.relationFilter.selectedDepth + 1,
        direction: this.relationFilter.direction
      };
      this.param = param;
      const id = this.$route.params.id;
      // this.$route.params.id 这里用这个是因为刷新时computed的guid值还没变过来
      relationshipService.getTableLineage(id, param).then((res) => {
        // 重复请求处理
        if (!_.isEqual(param, this.param)) {
          return;
        }
        this.isSvgLoading = false;
        // 移除之前的元素
        d3.select(this.$refs['g']).selectAll('*').remove();
        this.lineageData = res;
        if (res.relations.length) {
          let guidEntityMap = {};
          res.entities.forEach(item => {
            guidEntityMap[item.guid] = item;
          });
          res.guidEntityMap = guidEntityMap;
          this.generateData(res.relations, res.guidEntityMap);
        }
      }).catch((res) => {
        this.isSvgLoading = false;
      });
    },
    /**
     * 创建数据
     */
    generateData: function (relations, guidEntityMap) {
      var that = this;

      this.g = new dagreD3.graphlib.Graph()
        .setGraph({
          nodesep: 50,
          ranksep: 90,
          rankdir: 'LR',
          marginx: 20,
          marginy: 20,
          transition: function transition(selection) {
            return selection.transition().duration(500);
          }
        })
        .setDefaultEdgeLabel(function () {
          return {};
        });

      function makeNodeObj(relationObj) {
        var obj = {};
        obj['shape'] = 'img';
        obj['typeName'] = relationObj.typeName;
        obj['label'] = relationObj.displayText.length > 18 ? relationObj.displayText.substr(0, 17) + '...' : relationObj.displayText;
        obj['toolTipLabel'] = relationObj.displayText;
        obj['id'] = relationObj.guid;
        obj['isLineage'] = true;
        obj['queryText'] = relationObj.queryText;
        obj['isProcess'] = relationObj.process;
        if (relationObj.status) {
          obj['status'] = relationObj.status;
        }

        return obj;
      }

      _.each(relations, function (obj, index) {
        if (!that.fromToObj[obj.fromEntityId]) {
          that.fromToObj[obj.fromEntityId] = makeNodeObj(guidEntityMap[obj.fromEntityId]);
          that.g.setNode(obj.fromEntityId, that.fromToObj[obj.fromEntityId]);
        }
        if (!that.fromToObj[obj.toEntityId]) {
          that.fromToObj[obj.toEntityId] = makeNodeObj(guidEntityMap[obj.toEntityId]);
          that.g.setNode(obj.toEntityId, that.fromToObj[obj.toEntityId]);
        }
        const styleObj = {
          fill: 'none',
          stroke: '#8bc152',
          width: 2
        };
        that.g.setEdge(obj.fromEntityId, obj.toEntityId, { 'arrowhead': 'arrowPoint', lineInterpolate: 'basis', 'style': 'fill:' + styleObj.fill + ';stroke:' + styleObj.stroke + ';stroke-width:' + styleObj.width + '', 'styleObj': styleObj });
      });

      if (this.fromToObj[this.guid]) {
        this.fromToObj[this.guid]['isLineage'] = false;
        this.checkForLineageOrImpactFlag(relations, this.guid);
      }
      this.createGraph();
    },
    checkForLineageOrImpactFlag: function (relations, guid) {
      const that = this;
      const nodeFound = _.filter(relations, { 'fromEntityId': guid });
      if (nodeFound.length) {
        _.each(nodeFound, function (node) {
          if (!node['traversed']) {
            node['traversed'] = true;
            that.fromToObj[node.toEntityId]['isLineage'] = false;
            const styleObj = {
              fill: 'none',
              stroke: '#fb4200',
              width: 2
            };
            that.g.setEdge(node.fromEntityId, node.toEntityId, { 'arrowhead': 'arrowPoint', lineInterpolate: 'basis', 'style': 'fill:' + styleObj.fill + ';stroke:' + styleObj.stroke + ';stroke-width:' + styleObj.width + '', 'styleObj': styleObj });
            that.checkForLineageOrImpactFlag(relations, node.toEntityId);
          }
        });
      }
    },
    /**
     * 将图形放置到中间
     */
    setGraphZoomPositionCal: function (argument) {
      let initialScale = 1.2;
      const svgEl = $('.table-svg');
      const graphWidth = this.g.graph().width + 80;
      const graphHeight = this.g.graph().height + 0;
      const width = svgEl.width();
      const height = svgEl.height();
      initialScale = Math.max(Math.min(width / graphWidth, height / graphHeight), 0.5);
      let translateValue = [(width / 2) - ((graphWidth * initialScale) / 2), (height / 2) - ((graphHeight * initialScale) / 2)];
      this.zoom.transform(d3.select(this.$refs['table-svg']), d3.zoomIdentity.translate(translateValue[0], translateValue[1]).scale(initialScale));
    },
    /**
     * 获取对应照片
     */
    getCurrentImg({id, isSelect, isProcess, status}) {
      if (isSelect) {
        return require('../../../../../public/images/icon-table-select.png');
      } else {
        if (isProcess) {
          if (this.entityStateReadOnly[status]) {
            return require('../../../../../public/images/icon-gear-delete.png');
          } else if (id === this.guid) {
            return require('../../../../../public/images/icon-gear-active.png');
          } else {
            return require('../../../../../public/images/icon-gear.png');
          }
        } else {
          if (this.entityStateReadOnly[status]) {
            return require('../../../../../public/images/icon-table-delete.png');
          } else if (id === this.guid) {
            return require('../../../../../public/images/icon-table-active.png');
          } else {
            return require('../../../../../public/images/icon-table.png');
          }
        }
      }
    },
    createGraph: function () {
      const that = this;
      const width = $('.table-svg').width();
      const height = $('.table-svg').height();
      this.g.nodes().forEach(function (v) {
        var node = that.g.node(v);
        // Round the corners of the nodes
        if (node) {
          node.rx = node.ry = 5;
        }
      });
      // Create the renderer
      var render = new dagreD3.render();
      // Add our custom arrow (a hollow-point)
      render.arrows().arrowPoint = function normal(parent, id, edge, type) {
        var marker = parent.append('marker')
          .attr('id', id)
          .attr('viewBox', '0 0 10 10')
          .attr('refX', 9)
          .attr('refY', 5)
          .attr('markerUnits', 'strokeWidth')
          .attr('markerWidth', 10)
          .attr('markerHeight', 8)
          .attr('orient', 'auto');

        var path = marker.append('path')
          .attr('d', 'M 0 0 L 10 5 L 0 10 z')
          .style('stroke-width', 1)
          .style('stroke-dasharray', '1,0')
          .style('fill', edge.styleObj.stroke)
          .style('stroke', edge.styleObj.stroke);
        dagreD3.util.applyStyle(path, edge[type + 'Style']);
      };
      render.shapes().img = function circle(parent, bbox, node) {
        if (node.id === that.guid) {
          var currentNode = true;
        }
        var shapeSvg = parent.append('circle')
          .attr('fill', 'url(#img_' + node.id + ')')
          .attr('r', currentNode ? '15px' : '14px')
          .attr('class', 'nodeImage ' + (currentNode ? 'currentNode' : (node.isProcess ? 'blue' : 'green')));

        parent.insert('defs')
          .append('pattern')
          .attr('x', '0%')
          .attr('y', '0%')
          .attr('patternUnits', 'objectBoundingBox')
          .attr('id', 'img_' + node.id)
          .attr('width', '100%')
          .attr('height', '100%')
          .append('image')
          .attr('xlink:href', function (d) {
            if (node) {
              return that.getCurrentImg({
                id: node.id,
                status: node.status,
                isProcess: node.isProcess
              });
            }
          })
          .attr('x', '2')
          .attr('y', '2')
          .attr('width', currentNode ? '26' : '24')
          .attr('height', currentNode ? '26' : '24');

        node.intersect = function (point) {
          // return dagreD3.intersect.circle(node, points, point);
          return dagreD3.intersect.circle(node, currentNode ? 16 : 13, point);
        };
        return shapeSvg;
      };
      // Set up an SVG group so that we can translate the final graph.
      d3.select(this.$refs['table-svg'])
        .attr('viewBox', '0 0 ' + width + ' ' + height)
        .attr('enable-background', 'new 0 0 ' + width + ' ' + height);
      const svgGroup = d3.select(this.$refs['g']);

      var tooltip = d3Tip()
        .attr('class', 'd3-tip')
        .offset([10, 0])
        .html(function (d) {
          var value = that.g.node(d);
          var htmlStr = '';
          if (value.id !== that.guid) {
            htmlStr = "<h5 style='text-align: center;'>" + (value.isLineage ? 'Lineage' : 'Impact') + '</h5>';
          }
          htmlStr += "<h5 class='text-center'><span style='color:#359f89'>" + value.toolTipLabel + '</span></h5> ';
          if (value.typeName) {
            htmlStr += "<h5 class='text-center'><span>(" + value.typeName + ')</span></h5> ';
          }
          if (value.queryText) {
            htmlStr += "<h5>Query: <span style='color:#359f89'>" + value.queryText + '</span></h5> ';
          }
          return "<div class='tip-inner-scroll'>" + htmlStr + '</div>';
        });
      svgGroup.call(tooltip);
      render(svgGroup, this.g);
      // change text postion
      svgGroup.selectAll('g.nodes g.label')
        .attr('transform', 'translate(2,-30)');
      svgGroup.selectAll('g.nodes g.node')
        .on('mouseenter', function (d) {
          that.activeNode = true;
          this.getScreenCTM()
            .translate(+this.getAttribute('cx'), +this.getAttribute('cy'));
          $('.table-svg').find('.node').removeClass('active');
          $(this).addClass('active');

          // Fix
          var width = $('body').width();
          var currentELWidth = $(this).offset();
          var direction = 'e';
          if (((width - currentELWidth.left) < 330)) {
            direction = (((width - currentELWidth.left) < 330) && ((currentELWidth.top) < 400)) ? 'sw' : 'w';
            if (((width - currentELWidth.left) < 330) && ((currentELWidth.top) > 600)) {
              direction = 'nw';
            }
          } else if (((currentELWidth.top) > 600)) {
            direction = (((width - currentELWidth.left) < 330) && ((currentELWidth.top) > 600)) ? 'nw' : 'n';
            if ((currentELWidth.left) < 50) {
              direction = 'ne';
            }
          } else if ((currentELWidth.top) < 400) {
            direction = ((currentELWidth.left) < 50) ? 'se' : 's';
          }
          tooltip.direction(direction).show(d, this);
        })
        .on('click', function (d) {
          tooltip.hide(d);
          if (that.lineageData.guidEntityMap[d].process) {
            return;
          }
          if (d !== that.currentSelected) {
            $(that.$refs['g']).find('.current-select-node').removeClass('current-select-node');
            const currentSelectDom = $(that.$refs['g']).find('#img_' + that.currentSelected).find('image');
            currentSelectDom.attr('href', currentSelectDom.attr('before-href'));
            // 将currentSelected置为d
            that.$emit('selectChange', d);

            if (d !== that.guid) {
              $(that.$refs['g']).find('#' + d).addClass('current-select-node');
              const currentSelectDom = $(that.$refs['g']).find('#img_' + d).find('image');
              currentSelectDom.attr('before-href', currentSelectDom.attr('href'));
              currentSelectDom.attr('href', that.getCurrentImg({
                isSelect: true
              }));
            }
          }
        })
        .on('mouseleave', function (d) {
          that.activeNode = false;
          var nodeEL = this;
          setTimeout(function (argument) {
            if (!(that.activeTip || that.activeNode)) {
              $(nodeEL).removeClass('active');
              tooltip.hide(d);
            }
          }, 400);
        });
      $('body').on('mouseover', '.d3-tip', function (el) {
        that.activeTip = true;
      });
      $('body').on('mouseleave', '.d3-tip', function (el) {
        that.activeTip = false;
        $('.table-svg').find('.node').removeClass('active');
        tooltip.hide();
      });

      // Center the graph
      this.setGraphZoomPositionCal();
    },
    /**
     * 设置svg缩放
     */
    zoomed() {
      d3.select(this.$refs['g']).attr('transform', d3Selection.event.transform);
    }
  }
};
</script>



