import http from 'src/utils/http';

export function getColumnInfo(param) {
  return http.post('metadata/table/column', param);
}

export function updateColumnInfo(param) {
  return http.post('metadata/update/table/column', param);
}

export function getColumnPortraitInfo(id, param) {
  return http.post(`table/column/portrait/${id}`, param);
}



