<template>
  <div
    v-loading="isSvgLoading"
    class="field-relation-chart"
  >
    <svg
      ref="table-svg"
      width="100%"
      height="100%"
      class="table-svg"
    >
      <text
        v-if="!isSvgLoading && lineageData && !lineageData.relations.length"
        x="50%"
        y="50%"
        alignment-baseline="middle"
        text-anchor="middle"
      >没有字段血缘数据</text>
      <g ref="g" />
    </svg>
  </div>
</template>

<script>
import './fieldRelationChart.scss';
import * as relationshipService from 'src/service/metadata-management/relationship';
import $ from 'jquery';
import _ from 'lodash';
import platform from 'platform';
import dagreD3 from 'dagre-d3';
import * as d3Selection from 'd3-selection';
import * as d3Zoom from 'd3-zoom';
import d3Tip from 'd3-tip';
const d3 = Object.assign({}, d3Selection, d3Zoom);
export default {
  props: {
    // 当前选中的guid
    currentSelected: {
      type: Object,
      default() {
        return {};
      }
    },
    // 当前的guid
    guid: {
      type: String,
      default: ''
    },
    relationFilter: {
      type: Object,
      default() {
        return {};
      }
    }
  },
  data() {
    return {
      isSvgLoading: false,
      param: {},
      fromToObj: {},
      // 当前选中字段的关联关系
      relatedNodes: [],
      // 当前选中字段
      select: null,
      // 返回的字段血缘数据
      lineageData: null,
      activeTip: false,
      g: null,
      zoom: null
    };
  },
  mounted() {
    if (platform.name === 'IE') {
      $('.table-relation-chart').css('opacity', '0');
    }
    this.zoom = d3.zoom().scaleExtent([0.5, 6]);
    d3.select(this.$refs['table-svg']).call(this.zoom.on('zoom', this.zoomed));
    this.getFieldLineage();
  },
  destroyed() {
    d3.select('.d3-tip').remove();
  },
  methods: {
    /**
     * 搜索字段
     */
    search(searchText) {
      if (!this.lineageData || !this.lineageData.relations.length) {
        return;
      }
      this.relatedNodes = [];
      this.reRender(searchText);
    },
    /**
     * 获取指定节点的关联节点
     */
    setRelatedNodes(nodeId) {
      this.relatedNodes = [];
      if (nodeId) {
        this.cicleRelatedNodes(nodeId);
      }
      return this.relatedNodes;
    },
    /**
     * 循环关联数组
     * nodeId 节点id
     * 要查找的方向
     */
    cicleRelatedNodes(nodeId, direction) {
      // 没有direction 为查找关联的指定初始节点
      if (!direction) {
        // 将该节点加入到关联节点数组中
        this.relatedNodes.indexOf(nodeId) === -1 && this.relatedNodes.push(nodeId);
        // 获取该节点之前节点
        const predecessorsNodesArr = this.g.predecessors(nodeId);
        predecessorsNodesArr.forEach(item => {
          if (this.relatedNodes.indexOf(item) === -1) {
            this.relatedNodes.push(item);
            this.cicleRelatedNodes(item, 'predecessors');
          }
        });
        // 获取该节点之后节点
        const successorsNodesArr = this.g.successors(nodeId);
        successorsNodesArr.forEach(item => {
          if (this.relatedNodes.indexOf(item) === -1) {
            this.relatedNodes.push(item);
            this.cicleRelatedNodes(item, 'successors');
          }
        });
      } else {
        const directionNodesArr = this.g[direction](nodeId);
        directionNodesArr.forEach(item => {
          if (this.relatedNodes.indexOf(item) === -1) {
            this.relatedNodes.push(item);
            this.cicleRelatedNodes(item, direction);
          }
        });
      }
    },
    /**
     * 获取字段血缘关系
     */
    getFieldLineage() {
      this.isSvgLoading = true;
      const param = {
        depth: this.relationFilter.selectedDepth + 1,
        direction: this.relationFilter.direction
      };
      this.param = param;
      const id = this.$route.params.id;
      // this.$route.params.id 这里用这个是因为刷新时computed的guid值还没变过来
      relationshipService.getColumnLineage(id, param).then((res) => {
        // 重复请求处理
        if (!_.isEqual(param, this.param)) {
          return;
        }
        this.isSvgLoading = false;
        this.lineageData = res;
        this.generateData(res.relations, res.entities);
      }).catch((res) => {
        this.isSvgLoading = false;
      });
    },
    /**
     * 刷新图形
     */
    refresh() {
      this.generateData(this.lineageData.relations, this.lineageData.entities);
    },
    /**
     * 创建数据
     */
    generateData: function (relations, entities) {
      this.select = null;
      this.relatedNodes = [];
      this.fromToObj = {};

      // 移除之前的元素
      d3.select(this.$refs['g']).selectAll('*').remove();

      if (!relations.length) {
        return;
      }

      this.g = new dagreD3.graphlib.Graph({
        compound: true
      })
        .setGraph({
          // 相邻字段上下的距离
          nodesep: 20,
          // 库之间的距离
          ranksep: 50,
          // 父级和子集之间的上下距离
          edgeSep: 20,
          rankdir: 'LR',
          marginx: 0,
          marginy: 0,
          transition: function transition(selection) {
            return selection.transition().duration(500);
          }
        })
        .setDefaultEdgeLabel(function () {
          return {};
        });

      this.setNodesAndEdges(relations, entities);

      this.createGraph();
    },
    makeNodeObj(relationObj) {
      var obj = {};
      obj['dbName'] = relationObj.dbName;
      obj['dbGuid'] = relationObj.dbGuid;
      obj['tableGuid'] = relationObj.tableGuid;
      obj['tableName'] = relationObj.tableName;
      obj['guid'] = relationObj.guid;
      obj['columnName'] = relationObj.columnName;
      return obj;
    },
    /**
     * 设置点和线
     */
    setNodesAndEdges(relations, entities, searchText) {
      var that = this;

      // 设置点
      _.each(this.lineageData.entities, (tableItem, index) => {
        // 是否是当前查询的table
        const isCurrentTable = tableItem.tableGuid === this.guid;
        // 是否是当前选中的table
        const isSelectTable = tableItem.tableGuid === that.currentSelected.tableGuid;
        const defaultDbShowColor = tableItem.dbStatus === 'DELETED' ? '#e8e8e8' : '#a1cdf5';
        const defaultTableShowColor = tableItem.tableStatus === 'DELETED' ? '#fbfbfb' : '#c6e1f9';
        // 父节点要画在子节点前边，
        that.g.setNode(tableItem.dbGuid + tableItem.tableGuid, {
          labelType: 'svg',
          label: that.getSvgLabel(tableItem.dbName, 13),
          clusterLabelPos: 'top',
          style: 'rx:10;ry:10;fill: ' + (isCurrentTable ? '#8DC056' : (isSelectTable ? '#5da1e0' : defaultDbShowColor)),
          data: tableItem.dbName
        });
        that.g.setNode(tableItem.tableGuid, {
          labelType: 'svg',
          label: that.getSvgLabel(tableItem.tableName, 11, true),
          clusterLabelPos: 'top',
          style: 'rx:10;ry:10;fill: ' + (isCurrentTable ? '#bad999' : (isSelectTable ? '#9dc6ec' : defaultTableShowColor)),
          data: tableItem.tableName
        });
        that.g.setParent(tableItem.tableGuid, tableItem.dbGuid + tableItem.tableGuid);
      });
      _.each(entities, function (obj, index) {
        that.fromToObj[obj.guid] = that.makeNodeObj(obj);
        // 是否是当前被过滤的字段
        const isFilterField = searchText && obj.columnName.indexOf(searchText) !== -1;
        // 关联的节点展示颜色
        const relatedNodeShowColor = that.currentSelected.columnGuid === obj.guid ? 'select current-select' : 'select';
        const defaultColumnShowColor = obj.columnStatus === 'DELETED' ? 'delete-column' : '';
        that.g.setNode(obj.guid, {
          label: obj.columnName.length > 8 ? obj.columnName.substr(0, 8) + '...' : obj.columnName,
          padding: 0,
          rx: 5,
          ry: 5,
          // 字段的宽度控制
          height: 30,
          width: 120,
          class: (that.relatedNodes.indexOf(obj.guid) !== -1) ? relatedNodeShowColor : (isFilterField ? 'current-filter' : defaultColumnShowColor),
          data: that.fromToObj[obj.guid] // You can save your node data object here for later use during events
        });
        that.g.setParent(obj.guid, obj.tableGuid);
      });
      // 设置线
      _.each(relations, function (relation, index) {
        const isRelationEdge = that.relatedNodes.indexOf(relation.fromEntityId) !== -1 && that.relatedNodes.indexOf(relation.toEntityId) !== -1;
        that.g.setEdge(relation.fromEntityId, relation.toEntityId, {
          class: that.relatedNodes.length ? (isRelationEdge ? 'arrowhead-path-select' : 'arrowhead-path') : 'arrowhead-path-default',
          arrowheadClass: that.relatedNodes.length ? (isRelationEdge ? 'arrowhead-select' : 'arrowhead') : 'arrowhead-default'
        });
      });
    },
    /**
     * 创建对应labelText的svg节点
     */
    getSvgLabel(labelText, length, isTable) {
      const showText = labelText.length > length ? labelText.substr(0, length) + '...' : labelText;
      const svg = document.createElementNS('http://www.w3.org/2000/svg', 'svg');
      const svgLabel = document.createElementNS('http://www.w3.org/2000/svg', 'text');
      const tspan = document.createElementNS('http://www.w3.org/2000/svg', 'tspan');
      const use = document.createElementNS('http://www.w3.org/2000/svg', 'use');
      tspan.setAttributeNS('http://www.w3.org/XML/1998/namespace', 'xml:space', 'preserve');
      tspan.setAttribute('dy', '1em');
      tspan.setAttribute('x', '1');
      tspan.setAttribute('showText', labelText);
      tspan.setAttribute('style', 'font-size: 12px;font-weight:bold;' + (isTable ? 'fill:#626669;' : 'fill:#FAFFFF;'));
      tspan.textContent = showText;
      tspan.classList.add('cluster-label');
      use.setAttribute('href', '#icon-shuju');
      svgLabel.appendChild(tspan);
      svg.classList.add('icon');
      // svg.appendChild(use);
      svg.appendChild(svgLabel);
      return svg;
    },
    createGraph: function () {
      const that = this;
      const width = $('.table-svg').width();
      const height = $('.table-svg').height();
      this.g.nodes().forEach(function (v) {
        var node = that.g.node(v);
        // Round the corners of the nodes
        if (node) {
          node.rx = node.ry = 5;
        }
      });
      // Set up an SVG group so that we can translate the final graph.
      d3.select(this.$refs['table-svg'])
        .attr('viewBox', '0 0 ' + width + ' ' + height)
        .attr('enable-background', 'new 0 0 ' + width + ' ' + height);

      this.renderGraph();
    },
    /**
     * 渲染初始化好的图形，并绑定对应的事件
     * reRender 是否是重新渲染
     */
    renderGraph(reRender) {
      const that = this;
      if (reRender) {
        that.g.graph().transition = function transition(selection) {
          return selection.transition().duration(0);
        };
      }
      // Create the renderer
      var render = new dagreD3.render();
      const svgGroup = d3.select(this.$refs['g']);
      var tooltip = d3Tip()
        .attr('class', 'd3-tip')
        .offset([10, 0])
        .html(function (data) {
          var htmlStr = '';
          htmlStr += "<h5 class='text-center'><span>" + data.showText + '</span></h5> ';
          return "<div class='tip-inner-scroll'>" + htmlStr + '</div>';
        });
      svgGroup.call(tooltip);
      render(svgGroup, this.g);
      // change text postion
      // svgGroup.selectAll('g.nodes g.label')
      //   .attr('transform', 'translate(2,-30)');
      svgGroup.selectAll('g.nodes g.node, .cluster-label')
        .on('mouseenter', function (d) {
          that.activeNode = true;

          // Fix
          var width = $('body').width();
          var currentELWidth = $(this).offset();
          var direction = 'e';
          if (((width - currentELWidth.left) < 330)) {
            direction = (((width - currentELWidth.left) < 330) && ((currentELWidth.top) < 400)) ? 'sw' : 'w';
            if (((width - currentELWidth.left) < 330) && ((currentELWidth.top) > 600)) {
              direction = 'nw';
            }
          } else if (((currentELWidth.top) > 600)) {
            direction = (((width - currentELWidth.left) < 330) && ((currentELWidth.top) > 600)) ? 'nw' : 'n';
            if ((currentELWidth.left) < 50) {
              direction = 'ne';
            }
          } else if ((currentELWidth.top) < 400) {
            direction = ((currentELWidth.left) < 50) ? 'se' : 's';
          }
          const data = {
            showText: d ? that.g.node(d).data.columnName : this.getAttribute('showText')
          };
          tooltip.direction(direction).show(data, this);
        })
        .on('click', function (d) {
          tooltip.hide(d);
          that.setSelectField(that.g.node(d).data);
        })
        .on('mouseleave', function (d) {
          that.activeNode = false;
          setTimeout(function (argument) {
            if (!(that.activeTip || that.activeNode)) {
              tooltip.hide(d);
            }
          }, 400);
        });
      $('body').on('mouseover', '.d3-tip', function (el) {
        that.activeTip = true;
      });
      $('body').on('mouseleave', '.d3-tip', function (el) {
        that.activeTip = false;
        tooltip.hide();
      });
      if (!reRender) {
        // Center the graph
        this.setGraphZoomPositionCal();
      }
    },
    /**
     * 设置选中字段
     */
    setSelectField(field) {
      this.$emit('selectChange', field.tableGuid, field.guid);
      this.setRelatedNodes(field.guid);
      this.reRender();
    },
    /**
     * 重新渲染
     */
    reRender(searchText) {
      // 延时渲染，因为父组件当前选中字段的值还没及时改变
      this.$nextTick(() => {
        this.setNodesAndEdges(this.lineageData.relations, this.lineageData.entities, searchText);
        this.renderGraph(true);
      });
    },
    /**
     * 将图形放置到中间
     */
    setGraphZoomPositionCal: function (argument) {
      let initialScale = 1.2;
      const svgEl = $('.table-svg');
      const graphWidth = this.g.graph().width + 80;
      const graphHeight = this.g.graph().height + 0;
      const width = svgEl.width();
      const height = svgEl.height();
      initialScale = Math.max(Math.min(width / graphWidth, height / graphHeight), 0.5);
      let translateValue = [(width / 2) - ((graphWidth * initialScale) / 2), (height / 2) - ((graphHeight * initialScale) / 2)];
      this.zoom.transform(d3.select(this.$refs['table-svg']), d3.zoomIdentity.translate(translateValue[0], translateValue[1]).scale(initialScale));
    },
    /**
     * 设置svg缩放
     */
    zoomed() {
      d3.select(this.$refs['g']).attr('transform', d3Selection.event.transform);
    }
  }
};
</script>



