<template>
  <div class="number-slider">
    <el-slider
      v-model="select"
      :max="5"
      :show-tooltip="false"
      label="sdd"
      @change="change"
    />
    <div class="item-content">
      <div
        v-for="(i, index) in limitNumber"
        :key="index"
        :style="{left: index * (100 / 5) + '%'}"
        class="item"
      >
        {{ index + 1 }}
      </div>
    </div>
  </div>
</template>

<script>

export default {
  props: {
    limitNumber: {
      type: Number,
      default: 6
    },
    value: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      select: this.value
    };
  },

  mounted() {
  },

  methods: {
    change() {
      this.$emit('input', this.select);
      this.$emit('change', this.select);
    }
  }
};
</script>



