<template>
  <div
    v-loading="isLoading"
    class="metadata-management-overview relative"
  >
    <el-table
      :data="table.data"
      :max-height="maxHeight"
      stripe
    >
      <el-table-column
        v-for="(name) in table.columnNames"
        :key="name"
        :label="name"
        :min-width="getCharLength(name)*18"
        align="center"
        show-overflow-tooltip
        prop="filename"
      >
        <template slot-scope="scope">
          {{ scope.row[name] }}
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import './overview.scss';
import * as overviewService from 'src/service/metadata-management/overview';
export default {
  data() {
    return {
      maxHeight: document.body.clientHeight - 300,
      isLoading: false,
      // 普通字段
      table: {
        columnNames: [],
        data: []
      }
    };
  },
  mounted() {
    this.getTablePreview();
  },
  methods: {
    /**
     * 获取字符长度
     */
    getCharLength(str) {
      let len = 0;
      for (let i = 0; i < str.length; i++) {
        if (str.charCodeAt(i) > 127 || str.charCodeAt(i) === 94) {
          len += 2;
        } else {
          len++;
        }
      }
      return len;
    },
    getTablePreview() {
      this.isLoading = true;
      overviewService.getTablePreview({
        guid: this.$route.params.id,
        count: 25
      }).then((res) => {
        this.isLoading = false;
        this.table.columnNames = res.columnNames;
        this.table.data = res.lines;
      }).catch((res) => {
        this.isLoading = false;
      });
    }
  }
};
</script>




