
<template>
  <div class="metadata-management-statistics">
    <div
      v-show="!showHistoryView"
      class="history-detail"
    >
      <gs-form
        v-loading="isLoading"
        ref="form"
        class="relative"
        label-width="95px"
      >
        <gs-form-item
          label="日期："
          prop="database"
        >
          <span>{{ statisticsTodayInfo.date }}</span>
        </gs-form-item>
        <gs-form-item
          label="字段数量："
          prop="database"
        >
          <span>{{ statisticsTodayInfo.fieldNum }}</span>
        </gs-form-item>
        <gs-form-item
          label="文件个数："
          prop="database"
        >
          <span>{{ statisticsTodayInfo.fileNum }}</span>
        </gs-form-item>
        <gs-form-item
          label="数据量："
          prop="database"
        >
          <span>{{ statisticsTodayInfo.dataVolume }}</span>
        </gs-form-item>
        <gs-form-item
          label="今日增量："
          prop="database"
        >
          <span>{{ statisticsTodayInfo.dataIncrement }}</span>
        </gs-form-item>
        <gs-form-item
          label="数据来源："
          prop="database"
        >
          <div
            v-for="(source, index) in statisticsTodayInfo.sourceTable"
            :key="index"
          >
            <i
              class="iconfont icon-yuanshuju"
            />
            <span style="margin-left: 4px">{{ source.database }}</span>
            <i
              :style="{transform: 'rotate(-90deg)'}"
              class="gs-icon-down"
            />
            <a @click="toDetail(source.tableId)">
              <i
                class="iconfont icon-biaoge"
              />
              <span style="margin-left: 4px">{{ source.tableName }}
            </span></a>
          </div>
        </gs-form-item>
      </gs-form>
      <gs-button
        type="text-primary"
        @click="viewHistory"
      >查看历史信息</gs-button>
    </div>
    <div v-if="showHistoryView">
      <history @back="showHistoryView=false" />
    </div>
  </div>
</template>

<script>
import './statistics.scss';
import History from './history.vue';
import * as statisticsService from 'src/service/metadata-management/statistics';
export default {
  components: {
    History
  },
  data() {
    return {
      isLoading: false,
      showHistoryView: false,
      statisticsTodayInfo: {}
    };
  },
  mounted() {
    this.getTableStatToday();
  },
  methods: {
    /**
     * 跳转到表详情
     */
    toDetail(id) {
      this.$router.push({
        path: `/main/metadata/detail/${id}`,
        query: {
          module: 'table'
        }
      });
      this.$emit('refreshTableInfo');
    },
    /**
     * 查看历史信息
     */
    viewHistory() {
      this.showHistoryView = true;
    },
    getTableStatToday() {
      this.isLoading = true;
      statisticsService.getTableStatToday(this.$route.params.id).then((res) => {
        this.isLoading = false;
        this.statisticsTodayInfo = res;
      }).catch((res) => {
        this.isLoading = false;
      });
    }
  }
};
</script>




